import React from 'react';

export default class Account extends React.Component {
    render() {
        return (
            <React.Fragment>
                <button onClick={() => { if (confirm('    ?')) this.props.logout() }}></button>
                <span>, {this.props.user.name}</span>
            </React.Fragment>
        );
    }
};


